import { Component } from '@angular/core';
import * as THREE from 'three';

import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader.js';
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})

export class AppComponent  {
  name = 'angular9-threejs';

      constructor() {
// WEB GL Three Render & Sizing
        const renderer = new THREE.WebGLRenderer({antialias: true}); // Anti alias for smoof
        renderer.setSize( window.innerWidth , window.innerHeight );
        document.body.appendChild( renderer.domElement );
// Scene
        const scene = new THREE.Scene();
// Camera
        const camera = new THREE.PerspectiveCamera(75, window.innerWidth  / window.innerHeight, 1, 10000);
// Orbit Controls
        const controls = new OrbitControls( camera, renderer.domElement ); // Allows dragging of the scene, as well as scroll zoom.
// Loader for GLTF
        const loader = new GLTFLoader();

// Set Clear Background &  Do colours
        renderer.setClearColor(0xEEEEEE); // Ensure background is transparent for clearer debug
        renderer.outputEncoding = THREE.sRGBEncoding; // Much brighter lighting. Looks nicer than linear
        renderer.toneMappingExposure = 1.6; // Brightness, 1 default.

// Loader for GLTF - Web hosted files or Web hosted project files ONLY
        // loader.load('http://localhost:4200/assets/models/rpg_reptile_mage/scene.gltf', (gltf) => {
        loader.load('http://localhost:4200/assets/models/blend_blockmansign/scene.gltf', (gltf) => {
        // loader.load('http://localhost:4200/assets/models/blend_lowpoly_snake/LowPolSnakeGLTF.gltf', (gltf) => {
          const model = gltf.scene || gltf.scenes[0];
  // Centering test
          const box = new THREE.Box3().setFromObject( model );
          const center = box.getCenter( new THREE.Vector3() );
          const size = box.getSize(new THREE.Vector3()).length();

          model.position.x += ( model.position.x - center.x );
          model.position.y += ( model.position.y - center.y );
          model.position.z += ( model.position.z - center.z );

          camera.position.x += size / 2.0;
          camera.position.y += size / 5.0;
          camera.position.z += size / 6;
          camera.lookAt( new THREE.Vector3() ); // Ensures default camera view faces the OrbitControl centre
          scene.add( model );
        }, undefined);

// Light setup
        const color = 0xFFFFFF;
        const intensity = 0.6;
        const ambientLight = new THREE.AmbientLight(color, intensity);
        const light  = new THREE.DirectionalLight(color, intensity);

        light.position.set(0.5, 0, 0.5);

        camera.add(light);
        camera.add(ambientLight);
        scene.add(camera);
        renderer.toneMappingExposure = 1;
// Cube setup
        const geometry = new THREE.BoxGeometry( 1, 1, 1 );
        const material = new THREE.MeshBasicMaterial( { color: 0x338633 } );
        const cube = new THREE.Mesh( geometry, material );
        // scene.add( cube );

// Anim for cube spin
        const animate = function() {
        requestAnimationFrame( animate );

        cube.rotation.x += 0.01;
        cube.rotation.y += 0.01;

        renderer.render( scene, camera );
        };

        animate();

        }
      }
